body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.top{
  /* background-color: #b7c7fd; */
  text-align:right;
  z-index: 100;
  background-color: #EEEEEE;
}
.top a{
  font-family: "NSimSun";
  margin-right: 2em;
}
.App-header {
  /* background-color: #a1b6face; */
  background-image: url(/static/media/logo_right.5293ef6f.jpg);
  /* min-height: 30vh; */
  display: flex;
  flex-direction:row;
  align-items: center;
  flex-wrap: wrap;
  font-size: calc(8px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header a{
  margin-right: 2em;
}
.App-header img{
  height: 20vh;
}
.club_title{
  max-width: 40vw;
  float: left;
  font-size: 3.5vw;
  font-style:inherit;
  font-weight: bolder;
  font-family:"KaiTi";
  color: rgb(35, 69, 131);
}
.router-link{
  bottom: 5px;
}
.nav{
  background-color: #1890FF;
  text-align: right;
}
.nav a{
    margin: 0 30px 0 20px;
    font-size: 18px;
    line-height: 54px;
    color: #fff;
    height: 54px;
    display: inline-block;
    /* text-align: right; */
    /* float: right; */
}
.carousel img{
  height: 100%;
}

.signUpButton{
  margin: 0 auto;
}
.ant-card-bordered .ant-card-cover{
  width: 20%;
}
.personalCompetitonList .ant-card-body{
  display: flex;
}
.personalCompetitonList .ant-card-body button{
  display: block;
}
.home-title{
    position: relative;
    text-align: center;
    width: 350px;
    margin: 10px auto;
}
.home-title .hr {
    display: inline-block;
    position: absolute;
    top: 53%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #ccc;
}
i{
    font-style: italic;
}
.home-title .title {
    padding: 0 30px;
    background-color: #fff;
    font-size: 26px;
    position: relative;
}
.home-content{
    text-align: center;
}
hr{
    display: block;
    unicode-bidi: -webkit-isolate;
    unicode-bidi: isolate;
    -webkit-margin-before: 0.5em;
            margin-block-start: 0.5em;
    -webkit-margin-after: 0.5em;
            margin-block-end: 0.5em;
    -webkit-margin-start: auto;
            margin-inline-start: auto;
    -webkit-margin-end: auto;
            margin-inline-end: auto;
    overflow: hidden;
    border-style: inset;
    border-width: 1px;
}
h3{
    display: block;
    font-size: 1.7em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    font-weight: bold;
}
.content-title{
    color: #0C7ED9;
    text-align: center;
}
.content-date{
    text-align: center;
}
.content-body{
    display: block;
    width: 90%;
    margin: 0 auto;
    text-align: left;
}
.content-body span{
    font-weight: bolder;
}

